}



// Sass mixin and function to use rem units with pixel fallback.
// https://github.com/pierreburel/sass-rem
// -----------------------------------------------------------------------------

$rem_baseline: $font_size_base !default;
$rem_fallback: true !default;
$rem_px_only: false !default;

// list-separator polyfill by Hugo Giraudel (https://sass-compatibility.github.io/#list_separator_function)
@function rem-separator($list) {
  @if function-exists("list-separator") == true {
    @return list-separator($list);
  }

  $test-list: ();
  @each $item in $list {
    $test-list: append($test-list, $item, space);
  }

  @return if($test-list == $list, space, comma);
}

@mixin rem-baseline($zoom: 100%) {
  font-size: $zoom / 16px * $rem_baseline;
}

@function rem-convert($to, $values...) {
  $result: ();
  $separator: rem-separator($values);

  @each $value in $values {
    @if type-of($value) == "number" and unit($value) == "rem" and $to == "px" {
      $result: append($result, $value / 1rem * $rem_baseline, $separator);
    } @else if type-of($value) == "number" and unit($value) == "px" and $to == "rem" {
      $result: append($result, $value / ($rem_baseline / 1rem), $separator);
    } @else if type-of($value) == "list" {
      $result: append($result, rem-convert($to, $value...), $separator);
    } @else {
      $result: append($result, $value, $separator);
    }
  }

  @return $result;
}

@function rem($values...) {
  @if $rem_px_only {
    @return rem-convert(px, $values...);
  } @else {
    @return rem-convert(rem, $values...);
  }
}

@mixin rem($properties, $values...) {
  @if type-of($properties) == "map" {
    @each $property in map-keys($properties) {
      @include rem($property, map-get($properties, $property));
    }
  } @else {
    @each $property in $properties {
      @if $rem_fallback or $rem_px_only {
        #{$property}: rem-convert(px, $values...);
      }
      @if not $rem_px_only {
        #{$property}: rem-convert(rem, $values...);
      }
    }
  }
}


// CSS3 PROPERTIES
// --------------------------------------------------

// Border Radius
@mixin border-radius($radius: $base_border_radius) {
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  border-radius: $radius;
}

// Box sizing
@mixin box-sizing($boxmodel: border-box) {
  -webkit-box-sizing: $boxmodel;
  -moz-box-sizing: $boxmodel;
  box-sizing: $boxmodel;
}

// Default Drop shadows
@mixin box-shadow($shadow...) {
  -webkit-box-shadow: $shadow;
  -moz-box-shadow: $shadow;
  box-shadow: $shadow;
}

// Rem Drop shadows
@mixin rem-box-shadow($shadow...) {
  @include rem(-webkit-box-shadow, $shadow...);
  @include rem(-moz-box-shadow, $shadow...);
  @include rem(box-shadow, $shadow...);
}

// Optional hyphenation
@mixin hyphens($mode: auto) {
  word-wrap: break-word;
  -webkit-hyphens: $mode;
  -moz-hyphens: $mode;
  -ms-hyphens: $mode;
  -o-hyphens: $mode;
  hyphens: $mode;
}

// Transition
@mixin transition($property: all, $duration: 500ms, $timing-function: ease, $delay: 0ms) {
  -webkit-transition: $property $duration $timing-function $delay;
  -moz-transition: $property $duration $timing-function $delay;
  -ms-transition: $property $duration $timing-function $delay;
  -o-transition: $property $duration $timing-function $delay;
  transition: $property $duration $timing-function $delay;
}

// generic transform
@mixin transform($transforms) {
  -moz-transform: $transforms;
  -o-transform: $transforms;
  -ms-transform: $transforms;
  -webkit-transform: $transforms;
  transform: $transforms;
}

// rotate
@mixin rotate($deg) {
  @include transform(rotate(#{$deg}deg));
}

// skew
@mixin skew($x, $y) {
  -webkit-transform: skew(#{$x}deg, #{$y}deg);
  -moz-transform: skew(#{$x}deg, #{$y}deg);
  -ms-transform: skew(#{$x}deg, #{$y}deg);
  -o-transform: skew(#{$x}deg, #{$y}deg);
  transform: skew(#{$x}deg, #{$y}deg);
}

// Multicolumn
@mixin multicolumn($count: 2, $gap: 30px) {
  -moz-column-count: $count;
  @include rem('-moz-column-gap', $gap);
  -webkit-column-count: $count;
  @include rem('-webkit-column-gap', $gap);
  column-count: $count;
  @include rem('column-gap', $gap);
}

// Responsive SVGs 
// Apply the mixin to the div that surrounds your svg
// get the svg width and height from the viewbox attribute of the SVG
// see: http://mimoymima.com/sass-mixin-responsive-svgs/
@mixin responsive_svg($svgwidth, $svgheight, $containerwidth) {
  width: $containerwidth; // sizes the svg
  margin: 0 auto; // centers the svg
  height: 0;
  padding-top: #{($svgheight/$svgwidth)*$containerwidth};
  position: relative;
  svg {
    position: absolute;
    top: 0;
    left: 0;
  }
}


// Calculation
@mixin calc($property, $expression) {
  #{$property}: -webkit-calc(#{$expression});
  #{$property}: calc(#{$expression});
}


// PSEUDO-ICONS SVG
// --------------------------------------------------
// Common styles for icons used as before/after pseudo-elements
@mixin pseudo-icon($extend,
                   $position: 'before',
                   $display: inline-block,
                   $margin: 0,
                   $opacity: 1)
{
  @if not index('before' 'after', $position) {
    @warn "Position must be specified as either a 'before' or 'after'-element";
    @error "See above warning for stacktrace";
  }

  &::#{$position} {
    @extend #{$extend};
    content: '';
    display: $display;
    @if ( $margin != 0  ) { margin: $margin; }
    @if ( $opacity < 1 ) { opacity: $opacity; }
  }
}


// Include grunticon
// --------------------------------------------------
// Specify grunticon to include
@mixin grunticon($name) {
  @extend .#{$name};
}

@mixin grunticon-color($fill){
  path{
    fill: $fill;
  }
}


// Arrow icon - for opening and closing a menu
// Use only position: right to get it work
// --------------------------------------------------
@mixin single-arrow-icon($direction: 'down', $size: 'medium', $display: 'block', $color: $link_color) {
  position: relative;
  display: #{$display};

  &:before,
  &:after {
    position: absolute;
    content: "";
    background-color: $color;
    @include transition($property: all, $duration: 80ms, $timing-function: ease-in-out);
  }
  @if $size == "small" {
    @if $direction == "down" or $direction == "up" {
      @include rem('top', -5px);
      @include rem('right', 0);
      @include rem('height', 1px);
      @include rem('width', 11px);
    }
    @else if $direction == "right" or $direction == "left" {
      @include rem('top', 0-3px);
      @include rem('right', -4px);
      @include rem('height', 11px);
      @include rem('width', 1px);
    }

    &:before {
      @if $direction == "down" { height: 100%; width: 51%; top: 0; left: 0; @include skew(-45, 42); }
      @else if $direction == "up" { height: 100%; width: 51%; top: 0; left: 0; @include skew(45, -42); }
      @else if $direction == "right" { height: 51%; width: 100%; top: 0; left: 0; @include skew(42, -45); }
      @else if $direction == "left" { height: 51%; width: 100%; top: 0; left: 0; @include skew(-42, 45); }
    }
    &:after {
      @if $direction == "down" { height: 100%; width: 51%; top: 0; right: 0; @include skew(45, -42); left: inherit;}
      @else if $direction == "up" { height: 100%; width: 51%; top: 0; right: 0; @include skew(-45, 42); left: inherit; }
      @else if $direction == "right" { height: 51%; width: 100%; top: 48%; left: 0; @include skew(-42, 45); right: inherit; }
      @else if $direction == "left" { height: 51%; width: 100%; top: 48%; left: 0; @include skew(42, -45); right: inherit; }
    }
  }

  @if $size == "medium" {
    @if $direction == "down" or $direction == "up" {
      @include rem('top', -5px);
      @include rem('right', 0);
      @include rem('height', 1px);
      @include rem('width', 14px);
    }
    @else if $direction == "right" or $direction == "left" {
      @include rem('top', 0);
      @include rem('right', -4px);
      @include rem('height', 14px);
      @include rem('width', 1px);
    }

    &:before {
      @if $direction == "down" { height: 100%; width: 51%; top: 0; left: 0; @include skew(-45, 42); }
      @else if $direction == "up" { height: 100%; width: 51%; top: 0; left: 0; @include skew(45, -42); }
      @else if $direction == "right" { height: 51%; width: 100%; top: 0; left: 0; @include skew(42, -45); }
      @else if $direction == "left" { height: 51%; width: 100%; top: 0; left: 0; @include skew(-42, 45); }
    }
    &:after {
      @if $direction == "down" { height: 100%; width: 51%; top: 0; right: 0; @include skew(45, -42); left: inherit;}
      @else if $direction == "up" { height: 100%; width: 51%; top: 0; right: 0; @include skew(-45, 42); left: inherit; }
      @else if $direction == "right" { height: 51%; width: 100%; top: 48%; left: 0; @include skew(-42, 45); right: inherit; }
      @else if $direction == "left" { height: 51%; width: 100%; top: 48%; left: 0; @include skew(42, -45); right: inherit; }
    }
  }

  @else if $size == "large" {
    @if $direction == "down" or $direction == "up" {
      @include rem('top', -4px);
      @include rem('right', 0);
      @include rem('height', 2px);
      @include rem('width', 20px);
    }
    @else if $direction == "right" or $direction == "left" {
      @include rem('top', 0);
      @include rem('right', -4px);
      @include rem('height', 20px);
      @include rem('width', 2px);
    }

    &:before {
      @if $direction == "down" { height: 100%; width: 51%; top: 0; @include rem('left', 1px); @include skew(-45, 42); }
      @else if $direction == "up" { height: 100%; width: 51%; top: 0; @include rem('left', 1px); @include skew(45, -42); }
      @else if $direction == "right" { height: 51%; width: 100%; @include rem('top', 1px); left: 0; @include skew(42, -45); }
      @else if $direction == "left" { height: 51%; width: 100%; @include rem('top', 1px); left: 0; @include skew(-42, 45); }
    }
    &:after {
      @if $direction == "down" { height: 100%; width: 51%; top: 0; right: 0; @include skew(45, -42); left: inherit;}
      @else if $direction == "up" { height: 100%; width: 51%; top: 0; right: 0; @include skew(-45, 42); left: inherit; }
      @else if $direction == "right" { height: 51%; width: 100%; top: 50%; left: 0; @include skew(-42, 45); right: inherit; }
      @else if $direction == "left" { height: 51%; width: 100%; top: 50%; left: 0; @include skew(42, -45); right: inherit; }
    }
  }
}

// OUTLINE
// --------------------------------------------------
@mixin outline($outline-width: 3px,
               $outline-color: $link_outline_color,
               $outline-style: solid,
               $outline-offset: 1px) {

  outline: $outline-width $outline-color $outline-style;
  outline-offset: $outline-offset               ;
}