// Article
// -------------------------------------------------------------

.input--title {
    @include rem('font-size', 19px);
    line-height: ( 28px / 19px );
    width: 100%;
}
.input--title-error {
    color: $brand_danger;
    @include rem('font-size', 17px);
    line-height: ( 28px / 17px );
}

.textarea--excerpt {
    @include rem('padding', 21px 18px 46px);
    @include border-radius(0);
    @include box-shadow(none);
    resize: none;
    overflow: hidden;
}

.cke {
    border-color: $grey7 !important;

    .cke_top {
        border-bottom-color: $grey7 !important;
    }
    .cke_bottom {
        border-top-color: $grey7 !important;
    }

    .cke_contents {
    }

    &.cke_focus .cke_contents {
    }
}

.cke_focus{
    .cke_inner{
        @include outline($outline-offset: 2px);
    }
}


.edit-article-authors {

    &__input {
        width: 100%;
        @include rem('max-width', 360px);
    }

    &__connection {
        @include SourceSansProSemibold();
    }

    @media (min-width: $md_break) {
        @include flexbox();
        @include flex-wrap($value: wrap);

        &__input {
            @include rem('width', 260px);
            max-width: none;
        }
        &__connection {
            @include rem('margin', 8px 7px 0);
        }
        .error-text {
            @include flex-basis(100%);
            @include order($int: 10);
        }
    }

    @media (min-width: $lg_break) {
        &__input {
            @include rem('width', 250px);
        }
    }
    @media (min-width: $xl_break) {
        &__input {
            @include rem('width', 300px);
        }
    }
}

.excerpt_counter__area {
    position: relative;
}

.excerpt_counter {
    color: $grey7;
    @include rem('font-size', 17px);
    line-height: ( 24px / 17px );
    position: absolute;
    @include rem('right', 12px);
    @include rem('bottom', 7px);

    &.warn {
        color: $black;
    }

    &.super_warn {
        color: $brand_danger;
    }
}

