// Article list
// -------------------------------------------------------------


.editor-article-list {

    // Reset non-edit-styling
    .ece_module_container {
        border-bottom: 0 none;
        padding-bottom: 0;
        margin-bottom: 0;
    }

    &__item {
        @include rem('padding', 21px 21px 0);
        @include box-sizing();
        @include rem('border', 1px dashed $link_color);
        @include border-radius();
        @include rem('margin-bottom', 21px);
    }

    .escenic-artikkel {
        position: relative;
        @include rem('border', 1px solid rgba($link_color, 50/100));
        border-top: 0 none;
        border-bottom: 0 none;
        margin-bottom: 0;
        @include border-radius(0);

        // Dette arves fra __item
        //@include box-sizing();
        //@include rem('padding', 21px 21px 0);

        &:after {
            position: absolute;
            bottom: 0;
            display: block;
            @include rem('right', 21px);
            @include rem('left', 21px);
            content: "";
            @include rem('height', 1px);
            background-color: rgba($link_color, 50/100);
        }
    }
    // Style for the first escenic article item
    & > .escenic-artikkel {
        @include rem('margin-top', 100px);

        &:before {
            position: absolute;
            display: block;
            @include rem('top', -44px);
            @include rem('right', -1px);
            @include rem('left', -1px);
            content: "Escenic artikler";
            @include rem('padding', 8px 21px 0);
            @include box-sizing();
            @include rem('height', 44px);
            background-color: $water;
            @include rem('border', 1px solid rgba($link_color, 50/100));
            @include border-radius(3px 3px 0 0);
        }
    }
    // Reset style for all other escenic article items
    & > .escenic-artikkel ~ .escenic-artikkel {
        margin-top: 0;

        &:before {
            display: none;
        }
    }
    // Style for the last item
    .escenic-artikkel:last-child {
        @include rem('border-bottom', 1px solid rgba($link_color, 50/100));
        @include border-radius(0 0 3px 3px);

        &:after {
            display: none;
        }
    }

    // Flytt artikkelen opp / ned
    &__sort {
        display: block;
        @include rem('padding', 5px 0 5px 14px);
        background-color: $water;
        @include border-radius();
        line-height: (21px / 17px);
        @include rem('font-size', 17px);

        &__link-up,
        &__link-down {
          position: relative;
          @include rem('padding-left', 16px);
          @include rem('margin-right', 8px);
        }

        .icon-arrow-ordering {
          position: absolute;
          display: block;
          @include rem('top', -4px);
          @include rem('left', -7px);
          @include rem('width', 32px);
          @include rem('height', 32px);
        }

        &__link-down .icon-arrow-ordering {
          @include rem('top', -5px);
          transform: rotate(180deg);
        }

    }

    &__load-more{
        color: $white;
        @include rem('font-size', 24px);
        line-height: ( 28px / 24px );
        @include rem('padding', 6px 0 7px);
        text-align: center;
        background-color: $link_color;
        text-decoration: none;
        display: block;
        border-radius: $base_border_radius;

        &:hover,
        &:active{
            background-color: darken($link_color, 5);
            color: $white;
            text-decoration: none;
        }
    }

    &__menu-bar {
        padding: 0;
        background-color: $water;
        @include border-radius();
        display: flex;
        justify-content: space-between;
    }

    &__title{
        line-height: (21px / 17px);
        @include rem('font-size', 17px);
        @include rem('padding', 5px 12px 3px 14px);
    }

    &__menu{

        &--open{
            .editor-article-list__menu-list{
                display: block;
            }

            .editor-article-list__menu-button{
                border-bottom-right-radius: 0;
                & .arrow-icon{
                    @include single-arrow-icon($direction: 'up', $size: 'small', $display: 'inline-block');
                    @include rem('right', -20px);
                }
            }
        }

    }

    &__menu-button{
        display: block;
        background-color: $white;
        @include border-radius();
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
        @include rem('border', 1px solid $link_color);
        line-height: (21px / 17px);
        @include rem('font-size', 17px);
        @include rem('padding', 4px 20px 4px 12px);
        position: relative;
        @include rem('width', 100px);
        @include box-sizing();

        .arrow-icon{
            @include single-arrow-icon($direction: 'down', $size: 'small', $display: 'inline-block');
            @include rem('right', -20px);
        }

        &:hover,
        &:active{
            text-decoration: none;
            color: $link_color;
        }
    }

    &__menu-list{
        display: none;
        padding: 0;
        margin: 0;
        @include rem('border', 1px solid $spindle);
        position: absolute;
        min-width: 100px;
        @include rem-box-shadow(0 1px 4px rgba($black, 8/100));
        @include box-sizing();
    }

    &__menu-list-item{
        background-color: $white;
        line-height: (21px / 16px);
        @include rem('font-size', 16px);
        @include rem('border-bottom', 1px solid $spindle);
        list-style-image: none;


        a{
            display: block;
            @include rem('padding', 4px 12px 4px 12px);
        }

        &--danger a{
            color: $brand_danger;
        }

        &:last-child{
            border-bottom: none;
        }

    }
}