// CROPPIE STYLE SHEET
// ***********************************
// https://foliotek.github.io/Croppie/

.croppie-container {
    width: 100%;
    height: 100%;
}

.croppie-container .cr-image {
    z-index: -1;
    position: absolute;
    top: 0;
    left: 0;
    transform-origin: 0 0;
    max-height: none;
    max-width: none;
}

.croppie-container .cr-boundary {
    position: relative;
    overflow: hidden;
    margin: 0 auto;
    z-index: 1;
    width: 100%;
    height: 100%;
    background-color: #ccc;
    @include box-sizing();
}

.croppie-container .cr-viewport,
.croppie-container .cr-resizer {
    position: absolute;
    @include rem('border', 2px solid $white);
    margin: auto;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    // @include rem-box-shadow(0 0 2000px 2000px rgba($black, 50/100));
    @include rem-box-shadow(0 0 0 899px rgba($white, 80/100));
    z-index: 0;
}

.croppie-container .cr-viewport:focus {
    @include outline();
}

.croppie-container .cr-resizer {
  z-index: 2;
  box-shadow: none;
  pointer-events: none;
}

.croppie-container .cr-resizer-vertical,
.croppie-container .cr-resizer-horisontal {
  position: absolute;
  pointer-events: all;
}

.croppie-container .cr-resizer-vertical::after,
.croppie-container .cr-resizer-horisontal::after {
    display: block;
    position: absolute;
    @include box-sizing();
    @include rem('border', 1px solid $black);
    background: $white;
    @include rem('width', 10px);
    @include rem('height', 10px);
    content: '';
}

.croppie-container .cr-resizer-vertical {
  @include rem('bottom', -5px);
  cursor: row-resize;
  width: 100%;
  @include rem('height', 10px);
}

.croppie-container .cr-resizer-vertical::after {
    left: 50%;
    @include rem('margin-left', -5px);
}

.croppie-container .cr-resizer-horisontal {
  @include rem('right', -5px);
  cursor: col-resize;
  @include rem('width', 10px);
  height: 100%;
}

.croppie-container .cr-resizer-horisontal::after {
    top: 50%;
    @include rem('margin-top', -5px);
}

.croppie-container .cr-original-image {
    display: none;
}

.croppie-container .cr-vp-circle {
    border-radius: 50%;
}

.croppie-container .cr-overlay {
    z-index: 1;
    position: absolute;
    cursor: move;
    touch-action: none;
}

.croppie-container .cr-slider-wrap {
    width: 75%;
    @include rem('max-width', 414px);
    @include rem('margin', 7px auto 14px);
    text-align: center;
}

.croppie-result {
    position: relative;
    overflow: hidden;
}

.croppie-result img {
    position: absolute;
}

.croppie-container .cr-image,
.croppie-container .cr-overlay,
.croppie-container .cr-viewport {
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
}


// STYLING RANGE INPUT
// ***********************************
// http://brennaobrien.com/blog/2014/05/style-input-type-range-in-every-browser.html

.cr-slider {
    -webkit-appearance: none;
    // removes default webkit styles
    // fix for FF unable to apply focus style bug
    @include rem('width', 300px);
    // required for proper track sizing in FF
    max-width: 100%;
    @include rem('padding', 14px 7px);
    background-color: transparent;
    border-color: transparent;
    box-shadow: none;
    margin-bottom: 0;
}

.cr-slider::-webkit-slider-runnable-track {
    width: 100%;
    @include rem('height', 2px);
    background-color: $grey7;
    border: 0;
    @include border-radius(3px);
}

.cr-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: none;
    @include rem('height', 22px);
    @include rem('width', 36px);
    @include border-radius(6px);
    background-color: $white;
    @include rem('border', 2px solid $link_color);
    @include rem('margin-top', -10px);
    -webkit-box-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: 0 0.125rem 1.25rem rgba(0, 0, 0, 0.3);
}

.cr-slider:focus {
    @include outline();
    border-color: transparent;
    box-shadow: none;
}

.cr-slider:focus::-webkit-slider-runnable-track {
    // background: #ccc;
}

.cr-slider::-moz-range-track {
    width: 100%;
    @include rem('height', 2px);
    background-color: $grey7;
    border: 0;
    @include border-radius(3px);
}

.cr-slider::-moz-range-thumb {
    border: none;
    @include rem('height', 22px);
    @include rem('width', 36px);
    @include border-radius(6px);
    background-color: $white;
    @include rem('border', 2px solid $link_color);
    @include rem('margin-top', -10px);
    -moz-box-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0 0.125rem 1.25rem rgba(0, 0, 0, 0.3);
}

.cr-slider:-moz-focusring {
    @include outline();
    outline-offset: 0;
}

.cr-slider::-ms-track {
    width: 100%;
    @include rem('height', 2px);
    background: transparent;
    // remove bg colour from the track, we'll use ms-fill-lower and ms-fill-upper instead
	border-color: transparent; // leave room for the larger thumb to overflow with a transparent border
	border-width: 14px 0;
	color: transparent; // remove default tick marks
}
.cr-slider::-ms-fill-lower {
    background-color: $grey7;
    @include border-radius(3px);
}
.cr-slider::-ms-fill-upper {
    background-color: $grey7;
    @include border-radius(3px);
}
.cr-slider::-ms-thumb {
	border: none;
    @include rem('height', 22px);
    @include rem('width', 36px);
    @include border-radius(6px);
    background-color: $white;
    @include rem('border', 2px solid $link_color);
    @include rem('margin-top', -5px);
}
.cr-slider:focus::-ms-fill-lower {
    background-color: $grey7;
}
.cr-slider:focus::-ms-fill-upper {
    background-color: $grey7;
}


// Rotation Tools
// ***********************************
.cr-rotate-controls {
	position: absolute;
    @include rem('bottom', 5px);
    @include rem('left', 5px);
	z-index: 1;
}
.cr-rotate-controls button {
	border: 0;
	background: none;
}
.cr-rotate-controls i:before {
	display: inline-block;
	font-style: normal;
	font-weight: 900;
	font-size: 22px;
}
.cr-rotate-l i:before {
	content: '↺';
}
.cr-rotate-r i:before {
	content: '↻';
}
